import React from "react";
import { graphql } from "gatsby";
import BlogPost from "@components/page/BlogPost";
import ProjectPost from "@components/page/ProjectPost";
import { SeoContainer } from "@components/Container/seoContainer";
import { graphqlSinglePostType } from "@tool/types";
import NotFoundPage from "./404";

type HeadPropTypes = {
  data: graphqlSinglePostType;
};

type TemplatePropTypes = {
  data: graphqlSinglePostType;
};

type renderPostPropTypes = {
  [key: string]: React.ReactNode;
};

export default function Template({
  data, // this prop will be injected by the GraphQL query below.
}: TemplatePropTypes) {
  const { markdownRemark } = data; // data.markdownRemark holds your post data
  const { frontmatter, html } = markdownRemark;
  const { type = "default" } = frontmatter;

  const renderPost: renderPostPropTypes = {
    blog: <BlogPost data={{ ...frontmatter, html }} />,
    project: <ProjectPost data={{ ...frontmatter, html }} />,
    default: <NotFoundPage />,
  };

  return renderPost[type];
}
export const pageQuery = graphql`
  query ($id: String!) {
    markdownRemark(id: { eq: $id }) {
      html
      frontmatter {
        date(formatString: "MMMM DD, YYYY")
        path
        title
        type
        image_cover
      }
    }
  }
`;

export const Head = ({ data }: HeadPropTypes) => {
  const { markdownRemark } = data; // data.markdownRemark holds your post data
  const { frontmatter } = markdownRemark;
  const { title, path } = frontmatter;

  return <SeoContainer subtitle={title} pathname={path} />;
};
