import React from "react";
import styled from "styled-components";
import { mdCss } from "@layouts/mdCss";
import { colors, device } from "@layouts/styleConfig";
import Comments from "@components/Comments";
import { MdFrontMatter } from "@tool/types";

type ProjectPostPropTypes = {
  data: MdFrontMatter;
};

const Layout = styled.div.withConfig({ componentId: "ProjectPost_Layout" })`
  display: grid;
  grid-template-columns: repeat(12, minmax(0, 1fr));
  padding: 32px 16px;
  color: ${({ theme }) => theme.color};

  .post-title {
    font-size: 32px;
    font-weight: 800;
    grid-column-start: 3;
    grid-column: 1/-1;
  }
  .post-date {
    grid-column: 1/-1;
    color: ${({ theme }) => theme.postColor};
    margin-top: 8px;
  }

  img {
    grid-column: 1/-1;
    width: 100%;
    height: 200px;
    object-fit: cover;
    margin-top: 24px;
    border-radius: 16px;
  }

  .post-content {
    grid-column: 1/-1;
    color: ${({ theme }) => theme.contentColor};
    ${mdCss};
  }

  .post-comments {
    border-top: 0.5px solid ${({ theme }) => theme.color};
    margin-top: 32px;
    padding-top: 16px;
    grid-column: 1/-1;
  }

  .post-image-source {
    grid-column: 1/-1;
    font-size: 14px;
    font-style: italic;
    color: ${colors.grey};
  }

  @media ${device.tablet} {
    .post-title {
      grid-column: 2/-2;
      font-size: 36px;
    }
    .post-date {
      grid-column: 2/-2;
    }

    img {
      grid-column: 2/-2;
      height: 300px;
    }

    .post-content {
      grid-column: 2/-2;
    }

    .post-comments {
      margin-top: 64px;
      padding-top: 32px;
      grid-column: 2/-2;
    }

    .post-image-source {
      grid-column: 2/-2;
    }
  }

  @media ${device.laptop} {
    .post-title {
      grid-column: 3/-3;
      font-size: 40px;
    }
    .post-date {
      grid-column: 3/-3;
    }

    img {
      grid-column: 3/-3;
      height: 400px;
    }

    .post-content {
      grid-column: 3/-3;
    }

    .post-comments {
      grid-column: 3/-3;
    }

    .post-image-source {
      grid-column: 2/-2;
    }
  }
`;

export default function ProjectPost({ data }: ProjectPostPropTypes) {
  const {
    title,
    date,
    image_cover,
    // type,
    // tags,
    html,
  } = data;
  return (
    <Layout>
      <div className="post-title">{title}</div>
      <div className="post-date">{date}</div>
      <img src={image_cover} alt={title} />
      <span className="post-image-source">the image is from unsplash </span>
      <div
        className="post-content"
        dangerouslySetInnerHTML={{ __html: html ?? "" }}
      />
      <div className="post-comments">
        <Comments />
      </div>
    </Layout>
  );
}
